import { BaseInput } from './baseInput';

export enum BaseInputEventType {
    Change = 'change',
}

export class BaseInputEvent<T extends BaseInput<T>, S extends string, P = any> extends CustomEvent<P> {
    public target!: T;

    constructor(type: S, args) {
        super(type, { composed: true, bubbles: true, cancelable: true, ...args });
    }
}
