import { BaseElement, booleanConverter, html, nothing, property } from '@umetrics/sartorius-ui-base';
import { BaseInputProperties } from './baseInputProperties';

export abstract class BaseInput<S extends BaseInput<S, Value> | any, Value = string>
    extends BaseElement
    implements BaseInputProperties<Value>
{
    // Static members
    private static FeedbackMessage;

    // Public members
    @property({ type: String, reflect: false })
    label;
    @property({ type: Boolean, converter: booleanConverter, reflect: false })
    disabled;

    abstract get value(): Value;
    abstract set value(value: Value);

    @property({ type: String, attribute: 'feedback-type' })
    additionalFeedbackType;
    @property({ type: String, attribute: 'feedback-mess' })
    additionalFeedbackMessage;

    connectedCallback() {
        this.style.setProperty('display', 'none');
        const imports: any[] = this._getChildImports();

        imports.push({ module: () => import('@umetrics/sartorius-ui-feedback-message'), elem: 'FeedbackMessage' });
        BaseInput.dynSubRegister(imports, () => {
            super.connectedCallback();
            this.style.removeProperty('display');
        });
    }

    abstract focus(options?: FocusOptions);

    abstract blur();

    protected _getChildImports(): any[] {
        return [];
    }

    protected _renderLabel(baseClassName) {
        return this.label
            ? html`<span class="${baseClassName}-label" title=${this.label}>${this.label}</span>`
            : nothing;
    }

    protected _renderFeedback(baseClassName, validationText = '', validationType = 'failure') {
        return validationText || this.additionalFeedbackMessage
            ? html`
                <${BaseInput.FeedbackMessage.tag}
                    message-type="${this.additionalFeedbackType || validationType}"
                    class="${baseClassName}-feedback-message">
                    ${this.additionalFeedbackMessage || validationText || ''}
                </${BaseInput.FeedbackMessage.tag}>
            `
            : nothing;
    }
}
