import {
    BaseInput,
    booleanConverter,
    createClassName,
    defineElement,
    html,
    property,
    query,
} from '@umetrics/sartorius-ui-base-input';

import { switchStyles } from './switchStyle';
import { SwitchProperties } from './switchProperties';
import { version } from './metadata';
import { SwitchEvent, SwitchEventType } from './switchEvent';

@defineElement({
    tagName: 'switch',
    styles: switchStyles,
    version,
    //    sub: [TooltipService]
})
export class Switch extends BaseInput<Switch> implements SwitchProperties {
    // Static members
    private static TooltipService;

    // Private members

    // Protected members

    // Public members
    @property({ type: Boolean, reflect: true, converter: booleanConverter })
    checked = false;
    @property({ type: Boolean, converter: booleanConverter, attribute: 'descriptive-text' })
    descriptiveText = false;
    @property({ type: String, reflect: false })
    description = '';
    @property({ type: String, reflect: false, attribute: 'tooltip-text' })
    tooltipText = '';
    @property({ type: String, reflect: false })
    get value() {
        return '' + this.checked;
    }

    set value(value: string) {
        const oldValue = '' + this.checked;
        if (oldValue !== value) {
            this.checked = value === 'true';
            if (this._checkboxElement) {
                this._checkboxElement.checked = this.checked;
            }
            this.requestUpdate('value', oldValue); // Triggering re-render
        }
    }

    @query('input')
    private _checkboxElement;

    constructor() {
        super();
        this._handleKeyDown = this._handleKeyDown.bind(this);
    }

    focus(options?: FocusOptions) {
        if (this._checkboxElement) {
            this._checkboxElement.focus(options);
        }
    }

    blur() {
        if (this._checkboxElement) {
            this._checkboxElement.blur();
        }
    }

    connectedCallback() {
        super.connectedCallback();
        this.addEventListener('keydown', this._handleKeyDown);
    }

    disconnectedCallback() {
        this.removeEventListener('keydown', this._handleKeyDown);
        super.disconnectedCallback();
    }

    protected _getChildImports() {
        const imports: any[] = [];
        imports.push({
            module: () => import('@umetrics/sartorius-ui-tooltip-service'),
            elem: ['TooltipService'],
        });
        return imports;
    }

    protected render() {
        const [baseClassName, rootClasses] = createClassName(this.tagName.toLowerCase(), {
            disabled: this.disabled,
        });

        const tooltipHtml =
            this.tooltipText !== ''
                ? html`<${Switch.TooltipService.tag} object-id="switch-tooltip" location="top-right"><span>${this.tooltipText}</span></${Switch.TooltipService.tag}>`
                : null;
        const descriptiveHtmlOn = this.descriptiveText
            ? html`<div class="${baseClassName}-description-on">on</div>`
            : null;
        const descriptiveHtmlOff = this.descriptiveText
            ? html`<div class="${baseClassName}-description-off">off</div>`
            : null;
        const descriptionHtml =
            this.description !== ''
                ? html`<label class="${baseClassName}-description">${this.description}</label>`
                : null;
        return html`
            <label class="${rootClasses}" @change=${this._handleChange}>
                <div class="${baseClassName}-container">
                    ${descriptiveHtmlOff}
                    <div class="${baseClassName}-wrapper">
                        <input type="checkbox" .checked=${this.checked} ?disabled=${!!this.disabled} />
                        <div class="${baseClassName}-path"></div>
                        <div class="${baseClassName}-thumb" id="switch-tooltip"></div>
                        ${tooltipHtml}
                    </div>
                    ${descriptiveHtmlOn} ${descriptionHtml}
                </div>
            </label>
        `;
    }

    private _handleKeyDown(event: KeyboardEvent) {
        if (event.code === 'Enter' || event.code === 'Space') {
            event.preventDefault();
            this.checked = !this.checked;
            this.dispatchEvent(new SwitchEvent(SwitchEventType.Change, {}));
        }
    }

    private _handleChange() {
        this.checked = this._checkboxElement.checked;
        this.dispatchEvent(new SwitchEvent(SwitchEventType.Change, {}));
    }
}
