import {
    baseInputStyle,
    css,
    CSSResultArray,
    cssSelector,
    cssVariableAsVar,
    GenerateStyles,
} from '@umetrics/sartorius-ui-base-input';

export const switchStyles: GenerateStyles = (tagName) => {
    const { root, child, is } = cssSelector(tagName);

    const yellow = cssVariableAsVar('color_primary_1');
    const black = cssVariableAsVar('color_primary_3');
    const gray = cssVariableAsVar('color_secondary_3');
    const grayTen = cssVariableAsVar('color_secondary_10');
    const grayEight = cssVariableAsVar('color_secondary_8');
    const graySeven = cssVariableAsVar('color_secondary_7');

    return [
        baseInputStyle(tagName),
        css`
            ${root} {
                display: flex;
                flex-direction: column;
                align-items: flex-start;
            }

            ${child('container')} {
                display: flex;
                flex-direction: row;
                align-items: center;
            }

            ${child('wrapper')} {
                display: inline-block;
                position: relative;
            }

            ${child('description')} {
                margin-left: 16px;
                font-size: 16px;
                font-weight: medium;
                color: ${black};
            }
            ${child('description-on')} {
                margin-left: 16px;
                font-size: 16px;
                font-weight: medium;
                color: ${black};
            }
            ${child('description-off')} {
                margin-right: 16px;
                font-size: 16px;
                font-weight: medium;
                color: ${grayEight};
            }
            @media (hover: hover) {
                ${root}:hover input:not(:disabled) ~ ${child('thumb')} {
                    background: ${grayTen};
                }
            }

            ${root} input {
                width: 100%;
                height: 100%;
                left: 0;
                top: 0;
                margin: 0;
                position: absolute;
                -webkit-appearance: none;
                -moz-appearance: none;
                cursor: pointer;
                -o-appearance: none;
                appearance: none;
            }

            ${root} input:disabled {
                cursor: auto;
            }

            ${child('path')} {
                width: 33px;
                height: 8px;
                margin: 4px 0;
                background: ${gray};
                transition: background 0.2s;
            }

            ${child('thumb')} {
                position: absolute;
                left: 0;
                top: 0;
                width: 16px;
                height: 16px;
                cursor: pointer;

                background: ${black};
                transition: left 0.2s;
            }

            input:checked ~ ${child('thumb')} {
                left: calc(100% - 16px);
                transition: left 0.2s;
            }
            input:checked:enabled ~ ${child('path')} {
                background: ${yellow};
                transition: background 0.2s;
            }

            ${is('disabled')} ${child('description-off')} {
                color: ${graySeven};
            }
            ${is('disabled')} ${child('description-on')} {
                color: ${graySeven};
            }
            ${is('disabled')} ${child('description')} {
                color: ${graySeven};
            }
            input:disabled ~ ${child('thumb')} {
                cursor: auto;
                background: ${grayEight};
            }

            input:checked:disabled ~ ${child('path')} {
                cursor: auto;
                background: #cbbd33;
            }
            input:disabled ~ ${child('path')} {
                cursor: auto;
                background: ${gray};
            }

            ${root} input:focus {
                outline: none;
                border: none;
            }
            ${root} input:focus-visible {
                outline: none;
                border: none;
            }

            input:focus-visible ~ ${child('thumb')} {
                outline: none;
                border: 2px solid ${yellow};
            }

            /* Code working only for Safari */
            @media not all and (min-resolution: 0.001dpcm) {
                @supports (-webkit-appearance: none) {
                    ${root} input:focus {
                        outline: none;
                        border: 1px solid ${black};
                    }
                }
            }
        `,
    ] as CSSResultArray;
};
