import { css, cssSelector, cssVariableAsVar, GenerateStyles } from '@umetrics/sartorius-ui-base';

export const baseInputStyle: GenerateStyles = (tagName) => {
    const { child, is } = cssSelector(tagName);

    return [
        css`
            :host,
            :host::before,
            :host::after,
            :host *,
            :host *::before,
            :host *::after {
                box-sizing: border-box;
            }

            ${child('label')} {
                display: inline-block;
                text-align: left;
                font-size: 14px;
                margin-bottom: 7px;
                color: ${cssVariableAsVar('color_secondary_8')};
            }

            ${is('required')} ${child('label')}::after {
                content: ' *';
            }

            ${child('feedback-message')} {
                margin-top: 3px;
            }
        `,
    ];
};
